:root {
  --color-dark-text: #494450;
  --color-text: #615c69;
  --color-light-text: #d2bea6;
  --font-bebas: 'Bebas Neue', Arial, Helvetica, sans-serif;
  --bg-body: #f1e2cf;
  --bg-light: #e8d7c3;
  --bg-dark: #494450;
  --transition: 0.2s ease-in-out;
}

.cource-tab {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 30px;
}

.cource-tab__link {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  max-width: 300px;
  min-height: 80px;
  padding: 10px 60px;
  overflow: hidden;
  font-weight: 900;
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  color: var(--color-dark-text);
  text-decoration: none;
  background: var(--bg-body);
  border-radius: 20px;
  box-shadow: 0 6px 18px var(--color-light-text);
  transition: background var(--transition);
}

.cource-tab__link svg {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: auto;
  height: 100%;
  opacity: 0;
  transition: opacity var(--transition), fill var(--transition);
}

.cource-tab__link:hover svg {
  opacity: 0.1;
  fill: var(--color-dark-text);
}

.cource-tab__link:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.cource-tab__link:focus-visible svg {
  opacity: 0.05;
}

.cource-tab__link:active,
.cource-tab__link--active {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
}

.cource-tab__link:active svg,
.cource-tab__link--active svg {
  opacity: 0.1;
  fill: var(--color-light-text) !important;
}

.page-footer {
  --color-footer-text: #85818c;
  font-size: 12px;
  line-height: 16px;
  color: var(--color-footer-text);
  background-color: var(--bg-dark);
  flex-grow: 0!important;
}

.page-footer a {
  color: inherit;
}

.blank {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 520px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.form {
  width: 100%;
}

@media (min-width: 768px) {
  .cource-tab__link {
    max-width: 250px;
    min-height: 100px;
    padding: 15px 40px;
  }

  .page-footer__col {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  .page-footer__page-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .page-footer__social-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  .page-footer__social-item:not(:last-child) {
    margin-right: 15px;
  }

  .page-footer__social-link {
    display: flex;
    align-items: center;
    width: 20px;
    height: 20px;
  }

  .page-footer__social-link:focus-visible {
    outline-width: 2px;
    outline-style: double;
    outline-color: var(--color-footer-text);
    outline-offset: 2px;
  }

  .page-footer__social-link svg {
    fill: var(--color-footer-text);
  }

  .page-footer__social-link--instagram:hover svg {
    fill: url(#a);
  }

  .page-footer__social-link--vk svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--vk:hover svg {
    fill: #2787f5;
  }

  .page-footer__social-link--vk:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--youtube svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--youtube:hover svg {
    fill: #ff0000;
  }

  .page-footer__social-link--youtube:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--telegram svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--telegram:hover svg {
    fill: url(#a1);
  }

  .page-footer__social-link--telegram:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--facebook svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--facebook:hover svg {
    fill: #1877f2;
  }

  .page-footer__social-link--facebook:hover svg .center {
    fill: #fff;
  }
}

@media (max-width: 767px) {
  .page-footer__col {
    padding: 0;
  }

  .page-footer__nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  .page-footer__nav-item {
    position: relative;
    flex-grow: 1;
    box-sizing: border-box;
  }

  .page-footer__nav-item svg {
    display: block;
    margin: 13px auto 10px;
    fill: var(--color-text);
  }

  .page-footer__nav-item:not(:last-child) {
    border-right: 1px solid #504b58;
  }

  .page-footer__nav-link {
    display: block;
    padding: 10px 5px 8px;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    font-family: var(--font-bebas);
    text-align: center;
    color: var(--color-light-text);
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    transition: color var(--transition);
  }

  .page-footer__nav-link.active {
    color: var(--color-text);
  }

  .page-footer__nav-link:hover {
    color: var(--color-text);
  }

  .page-footer__nav-link:focus-visible {
    outline-width: 1px;
    outline-style: double;
    outline-color: #d1bda5;
    outline-offset: -2px;
  }

  .page-footer__nav-link.active::after {
    content: url(../img/icon/page-footer__nav-link-active.svg);
    position: absolute;
    top: -13px;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media (max-width: 700px) {
  .cource-tab {
    flex-direction: column;
    align-items: center;
  }
}